.root-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.name-box-container {
    width: 100%;
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

.middle-row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.name-text {
    font-family: 'SF Pro', sans-serif;
    font-size: 4vw;
    color: #42423e;
    margin-top: -1rem;
}

.subtitle-text {
    font-family: 'SF Pro', sans-serif;
    font-size: 2vw;
    font-weight: 500;
    color: #42423e;
}

.icon-row {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 2vw;
    margin-left: -2vw;
    gap: 0.2vw;
}

.head-shot {
    width: 54%;
    filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.15))
}